
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpRequest } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { YeuCauSuDungPTNModel } from 'src/app/shared/models/yeu-cau-su-dung-ptn.model';

@Injectable({
  providedIn: 'root',
})
export class YeuCauMuonThietBiService {
  constructor(private httpClient: HttpClient) {}

  getAllConst(): Observable<any> {
    let url = environment.api.url + 'Const/getAll';
    return this.httpClient.get(url);
  }
  add(yeuCau: YeuCauSuDungPTNModel): Observable<any> {
    let url = environment.api.url + 'LichSuMuonThietBi/add'
    return this.httpClient.post(url, yeuCau)
  }
  getAllLoaiHocVien(): Observable<any> {
    let url = environment.api.url + "LoaiHocVien/getAll"
    return this.httpClient.get(url)
    
  }
}
