import { Component, OnInit } from '@angular/core';
import {
  FormBuilder,
  FormGroup,
  Validators,
  FormControl,
} from '@angular/forms';
import { MatDatepickerInputEvent } from '@angular/material/datepicker';
import { AlertDialogComponent } from 'src/app/shared/pages/alert-dialog/alert-dialog.component';
import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { DatePipe } from '@angular/common';

import { YeuCauSuDungPTNModel } from 'src/app/shared/models/yeu-cau-su-dung-ptn.model';
import { YeuCauMuonThietBiService } from './yeu-cau-muon-thiet-bi.service';
import { LoaiHocVienModel } from 'src/app/shared/models/loai-hoc-vien.model';
@Component({
  selector: 'app-yeu-cau-muon-thiet-bi',
  templateUrl: './yeu-cau-muon-thiet-bi.component.html',
  styleUrls: ['./yeu-cau-muon-thiet-bi.component.scss'],
})
export class YeuCauMuonThietBiComponent implements OnInit {
  _loaiHocViens: LoaiHocVienModel[];
  _yeuCau = new YeuCauSuDungPTNModel();
  _hasValueStartDay = false;
  _hasValueEndDay = false;

  _minDate = new Date();
  _maxDate = new Date();
  campaignTwo: FormGroup;
  showProgressBar = false;
  thongTinCoBanGroup: FormGroup;
  deTaiGroup: FormGroup;
  HVCH_NCSGroup: FormGroup;
  camKetGroup: FormGroup;
  isEditable = true;

  myFilter = (d: Date | null): boolean => {
    const day = (d || new Date()).getDay();
    // Prevent Saturday and Sunday from being selected.
    return day !== 0 && day !== 6;
  };
  constructor(
    private _formBuilder: FormBuilder,
    public datepipe: DatePipe,
    private __service: YeuCauMuonThietBiService,

    private _alertDialog: MatDialog,
    private __snackbar: MatSnackBar
  ) {}

  ngOnInit() {
    this.thongTinCoBanGroup = this._formBuilder.group({
      hoTen: ['', Validators.required],
      monHoc: ['', Validators.required],
      hocKy: ['', Validators.required],
      nhomHoc: ['', Validators.email],
      soSV: ['', Validators.required],
    });
    this.deTaiGroup = this._formBuilder.group({
      tenDeTai: ['', Validators.required],
      tenGVHD: ['', Validators.required],
      thoiGianBatDau: ['', Validators.required],
      thoiGianKetThuc: ['', Validators.required],
    });
    this.HVCH_NCSGroup = this._formBuilder.group({
      tenHVCH_NCS: ['', Validators.nullValidator],
      maHVCH_NCS: ['', Validators.nullValidator],
    });
    this.camKetGroup = this._formBuilder.group({
      camKet: ['', Validators.required],
    });

    this._maxDate.setDate(this._minDate.getDate() + 30);
    this.campaignTwo = new FormGroup({
      start: new FormControl(new Date()),
      end: new FormControl(new Date()),
    });

    this.getDataLoaiHocViens();
  }

 
  private getDataLoaiHocViens() {
    this.showProgressBar = true
    this.__service.getAllLoaiHocVien().subscribe(
      (data) => {
        this.showProgressBar = false;
        if(data.Message){
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
          return
        }
        this._loaiHocViens = data;
      },
      (error) => {
        debugger;
        this.showProgressBar = false
      }
    );
  }

  confirm() {
    debugger;
    if (
      this.camKetGroup.value.camKet.toLocaleLowerCase() !=
      'trong quá trình thực hiện đề tài tại phòng thí nghiệm, Tôi xin cam kết thực hiện đúng nội qui và quy định của PTN'.toLocaleLowerCase()
    ) {
      debugger;
      this.__snackbar.open('Vui lòng nhập đúng cam kết', null, {
        duration: 2000,
      });
    } else {
      this.showProgressBar = true;
      this.__service.add(this._yeuCau).subscribe(
        (data) => {
          this.showProgressBar = false;
          if (data === 'true') {
            this.__snackbar.open('Đăng ký thành công', null, {
              duration: 2000,
            });
          } else {
            this.__snackbar.open(data, null, {
              duration: 2000,
            });
          }
          // let res = this._yeuCau
          // this.dialogMainRef.close(
          //   {
          //     data: res,
          //     result: true
          //   }
          // )
        },
        (error) => {
          //Khả năng là do trùng mã (không hiển thị)
          this.showProgressBar = false;
          // console.log(error)
          this._alertDialog.open(AlertDialogComponent, {
            disableClose: true,
            width: '17rem',
            data: {
              dataKey: 'Có lỗi xảy ra',
            },
          });
        }
      );
    }
  }

  addEventNgayBatDau(type: string, event: MatDatepickerInputEvent<Date>) {
    this._yeuCau.ThoiGianBatDau = this.datepipe.transform(event.value, 'short');
    this._hasValueStartDay = true;
  }
  addEventNgayKetThuc(type: string, event: MatDatepickerInputEvent<Date>) {
    this._yeuCau.ThoiGianKetThuc = this.datepipe.transform(
      event.value,
      'short'
    );
    this._hasValueEndDay = true;
    // const datChoNgayBatDau = new Date(this._yeuCau.ThoiGianBatDau).getDate()
    // if (this._selectedThietBiChiTiet.NgayBatDau && this._selectedThietBiChiTiet.NgayKetThuc) {
    //   const ngayBatDau = new Date(this._selectedThietBiChiTiet.NgayBatDau).getDate()
    //   if (datChoNgayBatDau < ngayBatDau) {
    //     const ngayKetThuc = new Date(this._selectedThietBiChiTiet.NgayKetThuc).getDate()
    //     const datChoNgayKetThuc = new Date(this._datCho.NgayKetThuc).getDate()
    //     if (datChoNgayKetThuc > ngayKetThuc) {
    //       //không được phép đặt chỗ như vậy
    //       this.fromInput.value = ''
    //       this.toInput.value = ''
    //       this.__snackbar.open('Vui lòng chọn ngày hợp lệ', null, {
    //         duration: 2000,
    //       });
    //     }
    //   }
    // }
  }
  addEventNgaySinh(type: string, event: MatDatepickerInputEvent<Date>) {
    this._yeuCau.NgaySinh = this.datepipe.transform(event.value, 'short');
  }
}
